.facts {
  box-sizing: border-box;
  position: relative;
  padding: 100px 60px 50px;

  &-title {
    font-size: 35px;
    line-height: 41px;
    text-align: center;
    color: #FFFFFF;
    margin-bottom: 30px;
  }

  &-slide {

    &-desc {
      margin-top: 40px;
      font-size: 27px;
      line-height: 32px;
      text-align: center;
      color: #FFFFFF;
    }
  }

  & > .swiper-button-prev, .swiper-button-next {
    color: #B78A3F;
  }

  & > .swiper-button-prev {
    left: 10px;
  }

  & > .swiper-button-next {
    right: 10px;
  }

  & > .swiper-button-disabled {
    opacity: 0;
  }

  &-pagination {
    display: flex;
    justify-content: center;
    margin-top: 66px;

    &-bullet {
      display: inline-block;
      width: 24px;
      height: 24px;
      border-radius: 50%;
      margin: 0 20px;
      background-color: transparent;
      border: 2px solid #B78A3F;
      opacity: 1;
      transition: 0.3s background-color;
    }

    & > .swiper-pagination-bullet-active {
      background-color: #B78A3F;
    }
  }
}
@media screen and (max-width: 992px){
  .facts {
    &-title {
      font-size: 30px;
      line-height: 30px;
      text-align: center;
      color: #FFFFFF;
      margin-bottom: 20px;
    }
    &-slide {
      &-image {
        width: 270px;
      }
      
      &-desc {
        margin-top: 40px;
        font-size: 27px;
        line-height: 32px;
        text-align: center;
        color: #FFFFFF;
      }
    }
   
    &-pagination {
      display: flex;
      justify-content: center;
      margin-top: 50px;
  
      &-bullet {
        display: inline-block;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        margin: 0 15px;
        background-color: transparent;
        border: 2px solid #B78A3F;
        opacity: 1;
        transition: 0.3s background-color;
      }
    }  
  }
}

@media (min-width: 768px) and (max-width: 991px){
  .facts {
    &-title {
      font-size: 30px;
      line-height: 30px;
      text-align: center;
      color: #FFFFFF;
      margin-bottom: 20px;
    }
    &-slide {
      &-image {
        width: 220px;
      }
      
      &-desc {
        margin-top: 30px;
        font-size: 20px;
        line-height: 20px;
        text-align: center;
        color: #FFFFFF;
      }
    }
   
    &-pagination {
      display: flex;
      justify-content: center;
      margin-top: 50px;
  
      &-bullet {
        display: inline-block;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        margin: 0 15px;
        background-color: transparent;
        border: 2px solid #B78A3F;
        opacity: 1;
        transition: 0.3s background-color;
      }
    }  
  }
}
