.quality {
    padding: 50px 0;
    &-title {
        font-size: 35px;
        line-height: 41px;
        color: #FFFFFF;
        text-align: center;
        margin-bottom: 42px;
    }
    &-map{
        height: 606px;
    }    
    &-text {
        font-size: 23px;
        line-height: 27px;
        color: #FFFFFF;
        margin: 42px 0 30px;
    }
    
}
@media screen and (max-width: 992px) {
    .quality {
        padding: 40px 0;
        &-title {
            font-size: 30px;
            line-height: 30px;
            color: #FFFFFF;
            text-align: center;
            margin-bottom: 40px;
        }
        &-map{
            width: 900px;
            height: 500px;
            margin: 0 auto;
        }    
        &-text {
            text-align: center;
            font-size: 20px;
            line-height: 20px;
            color: #FFFFFF;
            margin: 40px 0 30px;
        }
        
    }
}
@media (min-width: 768px) and (max-width: 991px) {
    .quality {
        padding: 30px 0;
        &-title {
            font-size: 30px;
            line-height: 30px;
            color: #FFFFFF;
            text-align: center;
            margin-bottom: 40px;
        }
        &-map{
            width: 690px;
            height: 400px;
            margin: 0 auto;
        }    
        &-text {
            text-align: center;
            font-size: 20px;
            line-height: 20px;
            color: #FFFFFF;
            margin: 40px 0 30px;
        }
        
    }
}