.hero {
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../../assets/hero-bg.jpg");
  background-size: cover;
  padding: 80px 0 178px;

  &-partners {
    display: flex;
    align-items: center;

    &-item {
      margin-left: 60px;

      &:first-child {
        margin-left: 0;
      }
    }
  }

  &-navigation {
    margin-top: 45px;

    &-links {
      text-decoration: none;
      font-size: 19px;
      line-height: 22px;
      color: #FFFFFF;
      margin-left: 74px;

      &:first-child {
        margin-left: 0;
      }
    }
  }

  &-title {
    width: 664px;
    font-size: 57px;
    line-height: 67px;
    color: #FFFFFF;
    margin: 108px 0 132px;
  }

  &-link {
    display: inline-block;
    padding: 35px 151px;
    text-decoration: none;
    font-weight: normal;
    font-size: 32px;
    line-height: 37px;
    color: #FFFFFF;
    background: #B78A3F;
  }
}
@media screen and (max-width: 992px) {
  .hero {
    padding: 100px;
    
    &-navigation {
      margin-top: 35px;
  
      &-links {
        text-decoration: none;
        font-size: 15px;
        line-height: 20px;
        color: #FFFFFF;
        margin-left: 50px;
  
        &:first-child {
          margin-left: 0;
        }
      }
    }

    &-title {
      width: 600px;
      font-size: 40px;
      line-height: 40px;
      color: #FFFFFF;
      margin: 90px 0 90px;
    }

    &-link {
      display: inline-block;
      padding: 25px 100px;
      text-decoration: none;
      font-weight: normal;
      font-size: 30px;
      line-height: 30px;
      color: #FFFFFF;
      background: #B78A3F;
    }
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  .hero {

    &-navigation {
      margin-top: 40px;
      display: flex;
      flex-direction: column;
  
      &-links {
        margin: 0 0 10px 0;
        text-decoration: none;
        font-size: 19px;
        line-height: 22px;
        color: #FFFFFF;
      }
    }
    &-title {
        width: 500px;
        font-size: 30px;
        line-height: 30px;
        color: #FFFFFF;
        margin: 80px 0 50px;
      }
  }
}